const dialogTitleStyles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});
const CustomeDialogTitle = withStyles(dialogTitleStyles)((props) => {
  const {children, classes, onClose, ...other} = props;
  return (
    <DialogTitle disableTypography className={classes.root} {...other}>
      <Typography className="invite-patient-dialog-title">{children}</Typography>
    </DialogTitle>
  );
});

class InvitePatientForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      patient: {
        name: '',
        email: '',
        appointmentDate: '',
        appointmentTime: '',
        dob: '',
        gender: '',
        chiefComplaint: ''
      },
      isFormSubmit: false,
      selectedDoctor: null,
      validAppointmentDate: false,
      validAppointmentTime: false,
      validDOB: false
    }
  }

  /**
   * Add custom validation rule ValidatorForm when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    ValidatorForm.addValidationRule(ONLY_CHARACTER_ACCEPT, UtilHelper.validateString);
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
    ValidatorForm.addValidationRule(BLOCK_SPECIAL_CHARACTER, UtilHelper.blockSpecialCharacter);
  };

  /**
   * Remove custom validation in ValidatorForm rule when component is unmount
   * AttachComponent method call when component is render
   */
  componentWillUnmount() {
    ValidatorForm.removeValidationRule(ONLY_CHARACTER_ACCEPT);
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
    ValidatorForm.removeValidationRule(BLOCK_SPECIAL_CHARACTER);
  }

  /**
   * Method executes on onChange action of form element
   */
  handleChange = (event) => {
    const patient = Object.assign({}, this.state.patient);
    patient[event.target.name] = event.target.value;
    this.setState({patient});
  };

  /**
   * Method executes on onChange action of autocomplete form element
   */
  handleChangeAutocomplete = (elementName, value) => {
    if (elementName === "doctorId") {
      this.setState({selectedDoctor: value})
    }
  };

  /**
   * Method executes on onChange action of date picker form element
   */
  handleDateChange = (date) => {
    const patient = Object.assign({}, this.state.patient);
    patient['appointmentDate'] = moment(date).format('DD/MM/YYYY');
    patient['appointmentTime'] = '';
    this.setState({patient, validAppointmentDate: true});
  };

  /**
   * Method executes on onChange action of date picker form element
   */
  handleDOBChange = (date) => {
    const patient = Object.assign({}, this.state.patient);
    patient['dob'] = moment(date).format('DD/MM/YYYY');
    this.setState({patient, validDOB: true});
  };

  /**
   * Method executes on onChange action of time picker form element
   */
  handleTimeChange = (time) => {
    const patient = Object.assign({}, this.state.patient);
    patient['appointmentTime'] = time;
    this.setState({patient, validAppointmentTime: true});
  };

  /**
   * Method executes on submit form action
   */
  handleSubmit = () => {
    this.setState({isFormSubmit: true}, () => {

      const patient = Object.assign({}, this.state.patient);
      this.setState({
        validAppointmentDate: patient.appointmentDate !== "",
        validAppointmentTime: patient.appointmentTime !== "",
        validDOB: patient.dob !== ""
      }, () => {
        this.onCreate();
      })
    })
  };

  /**
   * Method is used for invite patient/create consult
   */
  onCreate = () => {
    if (this.state.validAppointmentDate && this.state.validAppointmentTime && this.state.validDOB) {
      let patient = Object.assign({}, this.state.patient);
      if (Date.parse(new Date()) < Date.parse(patient.appointmentTime)) {
        if (this.props.currentRole.isCoordinator) {
          patient.doctorId = this.state.selectedDoctor.userId;
        } else {
        }
        this.props.onCreate(patient);
        this.onCancel();
      } else {
        this.props.showSnackbar(ConstantMessages.selectFeatureTime, Constants.SNACKBAR_VARIAINT.ERROR);
      }
    }
  };

  /**
   * Method executes on cancel submit form action
   */
  onCancel = () => {
    const patient = {
      name: '',
      email: '',
      appointmentDate: '',
      appointmentTime: '',
      chiefComplaint: '',
      dob: '',
      gender: '',
    };

    this.setState({
      patient, selectedDoctor: null,
      validAppointmentDate: false,
      validAppointmentTime: false,
      validDOB: false,
      isFormSubmit: false,

    }, () => {
      this.props.onCancel();
    })

  };

  render() {
    let {visible, doctorUsers} = this.props;
    const {patient} = this.state;

    return (
      <Dialog
        // fullScreen={fullScreen}
        open={visible}
        // onClose={this.onCancel}
        aria-labelledby="responsive-dialog-title"
      >
        <CustomeDialogTitle onClose={this.onCancel} id="responsive-dialog-title">Invite Patient</CustomeDialogTitle>
        <DialogContent>
          <ValidatorForm
            ref="form"
            onSubmit={() => this.handleSubmit()}
            noValidate
            onError={errors => console.log(errors)}
          >
            <Grid container spacing={2}>
              {
                this.props.currentRole.isCoordinator
                  ? (
                    <Grid item xs={12}>
                      <Autocomplete
                        id="size-small-outlined-multi"
                        size="small"
                        getOptionLabel={(option) => option.displayName}
                        name="doctorId"
                        options={doctorUsers.rows}
                        onChange={(e, value) => this.handleChangeAutocomplete("doctorId", value)}
                        disableClearable
                        value={this.state.selectedDoctor || null}
                        renderInput={(params) => {
                          return (
                            <TextValidator
                              name="roles" {...params}
                              fullWidth
                              required
                              label="Doctor" placeholder="Select Doctor"
                              validators={['required']}
                              errorMessages={['this field is required']}
                              value={this.state.selectedDoctor || null}
                            />
                          )
                        }}
                      />
                    </Grid>
                  )
                  : <></>
              }
              <Grid item xs={12}>
                <TextValidator
                  required
                  fullWidth
                  className="marginTop10"
                  label="Patient Name" onChange={this.handleChange}
                  name="name" value={patient.name || ''}
                  validators={['required', ONLY_BLANK_SPACES, ONLY_CHARACTER_ACCEPT, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD, BLOCK_SPECIAL_CHARACTER]}
                  errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.digit_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field, ConstantMessages.blockSpecialCharacter]}
                />
              </Grid>
              <Grid item xs={12}>
                <TextValidator
                  required
                  fullWidth
                  className="marginTop10"
                  label="Patient Email" onChange={this.handleChange}
                  name="email" value={patient.email || ''} validators={['required', 'isEmail']}
                  errorMessages={['this field is required', 'email is not valid']}
                />
              </Grid>
              {/* <Grid container spacing={2}> */}
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <Grid item xs={6}>
                    <DatePicker
                      required
                      className="appointmentDate"
                      margin="normal"
                      id="date-picker-dialog"
                      label="Appointment Date"
                      format="MM-dd-yyyy"
                      minDate={moment(new Date())}
                      value={patient.appointmentDate ? moment(patient.appointmentDate, 'DD-MM-YYYY') : null}
                      onChange={this.handleDateChange}
                    />
                    {
                      this.state.isFormSubmit && !this.state.validAppointmentDate ?
                        <div className="errorMessage">Please select appointment date</div> : ''
                    }
                  </Grid>
                  <Grid item xs={6}>
                    <TimePicker
                      required
                      margin="normal"
                      className="appointmentTime"
                      id="time-picker"
                      label="Appointment Time"
                      initialFocusedDate={patient.appointmentDate ? moment(patient.appointmentDate, 'DD-MM-YYYY') : null}
                      value={patient.appointmentTime ? patient.appointmentTime : null}
                      onChange={this.handleTimeChange}
                    />
                    {
                      this.state.isFormSubmit && !this.state.validAppointmentTime ?
                        <div className="errorMessage">Please select appointment time</div> : ''
                    }
                  </Grid>
                </MuiPickersUtilsProvider>
              {/* </Grid> */}
              {/* <Grid container spacing={2}> */}
                <Grid item xs={6}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                      <DatePicker
                          required
                          fullWidth
                          className="dob"
                          margin="normal"
                          id="date-picker-dialog"
                          label="Date Of Birth"
                          format="MM-dd-yyyy"
                          defaultChecked={"01-01-2000"}
                          initialFocusedDate={moment("01-01-2000")}
                          openTo="year"
                          views={["year", "month", "date"]}
                          maxDate={moment(new Date())}
                          value={patient.dob ? moment(patient.dob, 'DD-MM-YYYY') : null}
                          onChange={this.handleDOBChange}
                      />
                      {
                          this.state.isFormSubmit && !this.state.validDOB ?
                          <div className="errorMessage">Please select date of birth</div> : ''
                      }
                  </MuiPickersUtilsProvider>
                </Grid>
                <Grid item xs={6}>
                  <TextValidator
                      fullWidth
                      required
                      select
                      className="genderDropdown"
                      label="Gender" onChange={this.handleChange}
                      name="gender" value={patient.gender || ''} validators={['required']}
                      errorMessages={['this field is required']}
                  >
                      <MenuItem value="Male">Male</MenuItem>
                      <MenuItem value="Female">Female</MenuItem>
                  </TextValidator>
                </Grid>
              {/* </Grid> */}
              <Grid item xs={12}>
                <TextValidator
                  required
                  fullWidth
                  multiline
                  rows={3}
                  className="marginTop10"
                  label="Reason for visit" onChange={this.handleChange}
                  name="chiefComplaint" value={patient.chiefComplaint || ''}
                  validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
                  errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_area]}
                />
              </Grid>
              <Grid item xs={12}>
                <Button type="submit" variant="contained" color="primary"
                        className="invite-add-user-btn">Invite</Button>
                <Button variant="outlined" onClick={() => this.onCancel()}
                    className="cancel-invite-patient-btn close-button-text-color" color="default" >Close</Button>
              </Grid>
            </Grid>
          </ValidatorForm>
        </DialogContent>
      </Dialog>
    );
  }
}


