class RequestQueueConsults extends React.Component {

  constructor(props) {
    super(props);
    this.paginationParams = {
      page: 0,
      rowsPerPage: Constants.ROW_PER_PAGE_5,
    };
    this.state = {
      addNewConsultRequest: false,
      acceptRejectConsults: false,
      startRejectConsults: false,
      consultData: {},
      isRefresh: false,
      isReject: false,
      paginationParams: Object.assign({}, this.paginationParams)
    }
  };

  /**
   * Get all request queue consults method call when component is render
   * Get all doctors method call when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    if (this.props.loggedInUserPractice) {
      let data = Object.assign({}, this.state.paginationParams);
      this.props.getAllConsults(data);

      if (this.props.currentRole.isCoordinator) {
        data.PracticeId = this.props.loggedInUserPractice.PracticeId;
        this.props.getAllDoctorUsers(data);
        // this.props.getAllDoctorUsers(this.props.loggedInUserPractice.PracticeId);
      }

      if(this.props.loggedInUser.isVeterinary){
        this.props.getAllSpecies();
      }
    }
  };

  /**
   * Method is execute when componet is update
   * AttachComponent method call when component is render
   */
  componentWillUpdate = () => {
    const {isRefresh} = this.state;
    if (isRefresh) {
      this.setState({
        isRefresh: false
      }, () => {
        if (this.props.loggedInUserPractice) {
          let data = this.state.paginationParams;
          this.props.getAllConsults(data);
        }
      })
    }
  };

  /**
   * Get all request queue consults method call when component receive new props
   * AttachComponent method call when component receive new props
   */
  componentWillReceiveProps = (nextProps) => {
    if (this.props.loggedInUserPractice) {
      let data = Object.assign({}, this.state.paginationParams);
      if (nextProps.consults.rows && nextProps.consults.rows.length === 0 && data.page > 0) {
        data.page = data.page - 1;
        this.setState({
          paginationParams: data
        }, () => {
          this.props.getAllConsults(data);
        })
      } else {
      }
    }
  };

  /**
   * Method executes to show create new consults/invite patient popup
   */
  addNewConsultRequestDialog = (value) => {
    this.setState({
      addNewConsultRequest: value
    }, () => {
      if (!value) {
        let data = this.state.paginationParams;
        this.props.getAllConsults(data);

        this.props.callBackCounts();
      }
    })
  };

  /**
   * Method executes to show accept/reject consult popup
   */
  acceptRejectConsultDialog = (value, consultData, isReject) => {
    this.setState({
      acceptRejectConsults: value,
      consultData: consultData,
      isReject: isReject
    }, () => {
      let data = this.state.paginationParams;
      this.props.getAllConsults(data);
      this.props.callBackCounts();
    })
  };

  /**
   * Method executes to show emergency consult popup
   */
  startRejectConsultDialog = (value, consultData, isReject) => {
    this.setState({
      startRejectConsults: value,
      consultData: consultData,
      isReject: isReject
    }, () => {
      let data = this.state.paginationParams;
      this.props.getAllConsults(data);
      this.props.callBackCounts();
    })
  };

  /**
   * Method used for create new consults/invite patient
   */
  handleInvitePatient = (formData) => {
    const practiceId = this.props.loggedInUserPractice.PracticeId;
    if (!formData.doctorId) {
      const doctorId = this.props.loggedInUser.id;
      formData.doctorId = doctorId;
    } else {
    }
    const timezone = this.props.loggedInUserPractice.Practice.timezone;

    const date = moment(formData.appointmentDate, 'DD/MM/YYYY').format('YYYY-MM-DD');
    const time = moment(formData.appointmentTime).format('HH:mm');
    var appointmentTime = moment.tz(date + " " + time, timezone);

    formData.appointmentTime = appointmentTime;

    formData.practiceId = practiceId;
    formData.timezone = timezone;

    formData = UtilHelper.trimObject(formData);

    this.props.addConsult(formData).then((res) => {
      if (res) {
        this.addNewConsultRequestDialog(false)
      }
    });
  };


  /**
   * Method to accept consult
   */
  handleAcceptConsult = (formData) => {

    const timezone = this.props.loggedInUserPractice.Practice.timezone;
    const date = moment(this.state.consultData.appointmentDate, 'YYYY-MM-DD').format('YYYY-MM-DD');

    const time = moment(formData.appointmentTime).format('HH:mm');
    var appointmentTime = moment.tz(date + " " + time, timezone);

    formData.appointmentTime = appointmentTime;


    formData = UtilHelper.trimObject(formData);

    this.props.confirmConsult(formData).then((res) => {
      this.acceptRejectConsultDialog(false, {});
      this.startRejectConsultDialog(false, {});
    });
  };

  /**
   * Method to start consult
   */
  handleStartConsult = (formData) => {
    const timezone = this.props.loggedInUserPractice.Practice.timezone;
    const date = new Date(this.state.consultData.appointmentDate);

    const time = moment(formData.appointmentTime).format('HH:mm');
    var appointmentTime = moment.tz(date + " " + time, timezone);

    formData.appointmentTime = appointmentTime;

    formData = UtilHelper.trimObject(formData);
    this.props.confirmConsult(formData).then((res) => {
      if (res && this.props.currentRole.isDoctor) {
        this.props.startConsult(formData.consultId).then(res => {
          this.setState({
            isRefresh: true
          }, () => {
            sessionStorage.setItem('consult', formData.consultId);
            window.location = '/consult';
          });
        }, error => {
          this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
        });
      }
    });

    this.acceptRejectConsultDialog(false, {});
    this.startRejectConsultDialog(false, {});
  };

  /**
   * Method to reject consult
   */
  handleRejectConsult = (formData) => {
    if (formData.reason === "Other") {
      formData.rejectReason = formData.reasonText;
    } else {
      formData.rejectReason = formData.reason;
    }

    formData = UtilHelper.trimObject(formData);
    this.props.rejectConsult(formData);

    this.acceptRejectConsultDialog(false, {});
    this.startRejectConsultDialog(false, {});
  };

  render() {
    const {consults, loggedInUser, loggedInUserPractice} = this.props;
    const {paginationParams} = this.state;

    const columns = [
      {
        title: this.props.loggedInUser.isVeterinary?'Owner Name':'Patient Name',
        field: 'name',
        maxWidth: "20%",
        cellStyle: {
          width: "20%"
        },
        filterable: false,
        draggable: false,
        sorting: false,
        render: props => {
          if (props.patientDetails && props.patientDetails.name) {
            return (<AvatarCell heading={props.patientDetails.name} />);
          } else {
            return "-";
          }
        }
      },
      {
        title: 'Pet Name',
        field: 'petName',
        cellStyle: {
          width: "10%"
        },
        maxWidth: "10%",
        filterable: false,
        draggable: false,
        sorting: false,
        hidden: !this.props.loggedInUser.isVeterinary,
        render: props => {
          if (props.patientDetails && props.patientDetails.petName) {
            return (<span className="custom-column-heading">{ props.patientDetails.petName }</span>);
          } else {
            return "-";
          }
        }
      },
      {
        title: 'Preferences',
        fiels: 'availableSlots',
        maxWidth: "25%",
        cellStyle: {
          width: "25%"
        },
        filterable: false,
        draggable: false,
        sorting: false,
        render: props => {
          let displayDate = props.appointmentDate ? moment(props.appointmentDate).format("MM-DD-YYYY") : '-';
          if (props.isEmergency) {
            return (
              <span className="isEmergency">
              <SvgIcon className="isEmergency isEmergencyIcon">
                <EmergencyIcon className="isEmergency"/>
              </SvgIcon>
              <span className="custom-column-heading isEmergency">Emergency</span>
            </span>
            )
          } else {
            return (
              <span className="isTime">
              <SvgIcon className="isTime isTimeIcon">
                <TimeIcon className="isTime"/>
              </SvgIcon>
              <span className="custom-column-heading">{displayDate + " (" + props.availableSlots + ")"}</span>
              </span>
            )
          }
        }
      },
      {
        title: 'Doctor Name',
        field: 'doctorName',
        maxWidth: "15%",
        cellStyle: {
          width: "15%"
        },
        filterable: false,
        draggable: false,
        sorting: false,
        hidden: !this.props.loggedInUser.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_COORDINATOR),
        render: props => {
          return (
            <span className="custom-column-heading">
            {props.doctorName ? props.doctorName : '-'}
          </span>
          )
        }
      },
      {
        title: 'Payment',
        fiels: '',
        maxWidth: "10%",
        cellStyle: {
          width: "10%"
        },
        filterable: false,
        draggable: false,
        sorting: false,
        hidden: !loggedInUserPractice.Practice.allowPayment,
        render: props => {
          return (
            props.paymentStatus && props.paymentStatus.data
              ? (
                (props.paymentStatus.data.status === "succeeded" || props.paymentStatus.data.status === "canceled")
                  ? (
                    <span className="custom-column-heading paymentStatus">
                    {props.paymentStatus.data.status}
                  </span>
                  )
                  : (
                    <span className="custom-column-heading paymentStatus">
                    Pending
                </span>
                  )

              )
              : (
                <span className="custom-column-heading paymentStatus">
                Offline
              </span>
              )
          )
        }
      },
      {
        title: 'Actions',
        maxWidth: "20%",
        cellStyle: {
          width: "20%"
        },
        filterable: false,
        draggable: false,
        sorting: false,
        field: 'id',
        sortable: false,
        render: props => {
          if (loggedInUserPractice.Practice.allowPayment && props.paymentStatus.data) {
            if (props.paymentStatus.data.status !== "succeeded") {
              return "-";
            } else {
              if (props.isEmergency) {
                return (
                  <>
                    <Button size="small" className="acceptButtonOutlined" variant="outlined" color="primary"
                            onClick={() => this.startRejectConsultDialog(true, props, false)}>
                      <SvgIcon className="marginRight5">
                        <StartIcon color="primary"/>
                      </SvgIcon>
                      Start
                    </Button>
                    <Button size="small" className="rejectButtonOutlined" variant="outlined" color="primary"
                            onClick={() => this.startRejectConsultDialog(true, props, true)}>
                      <SvgIcon className="marginRight5">
                        <RejectIcon color="primary"/>
                      </SvgIcon>
                      Reject
                    </Button>
                  </>
                )
              } else {
                return (
                  <div className="">
                    {
                      props.status === Constants.CONSULTS_STATUS.PENDING
                        ? (
                          <>
                            <Button size="small" className="acceptButtonOutlined" variant="outlined" color="primary"
                                    onClick={() => this.acceptRejectConsultDialog(true, props, false)}>
                              <SvgIcon className="marginRight5">
                                <AcceptIcon color="primary"/>
                              </SvgIcon>
                              Accept
                            </Button>

                            <Button size="small" className="rejectButtonOutlined" variant="outlined" color="primary"
                                    onClick={() => this.acceptRejectConsultDialog(true, props, true)}>
                              <SvgIcon className="marginRight5">
                                <RejectIcon color="primary"/>
                              </SvgIcon>
                              Reject
                            </Button>
                          </>
                        )
                        : <></>
                    }
                  </div>
                )
              }
            }
          } else {
            if (props.isEmergency) {
              return (
                <>
                  <Button size="small" className="acceptButtonOutlined" variant="outlined" color="primary"
                          onClick={() => this.startRejectConsultDialog(true, props, false)}>
                    <SvgIcon className="marginRight5">
                      <StartIcon color="primary"/>
                    </SvgIcon>
                    Start
                  </Button>
                  <Button size="small" className="rejectButtonOutlined" variant="outlined" color="primary"
                          onClick={() => this.startRejectConsultDialog(true, props, true)}>
                    <SvgIcon className="marginRight5">
                      <RejectIcon color="primary"/>
                    </SvgIcon>
                    Reject
                  </Button>
                </>

              )
            } else {
              return (
                <div className="">
                  {
                    props.status === Constants.CONSULTS_STATUS.PENDING
                      ? (
                        <>
                          <Button size="small" className="acceptButtonOutlined" variant="outlined" color="primary"
                                  onClick={() => this.acceptRejectConsultDialog(true, props, false)}>
                            <SvgIcon className="marginRight5">
                              <AcceptIcon color="primary"/>
                            </SvgIcon>
                            Accept
                          </Button>

                          <Button size="small" className="rejectButtonOutlined" variant="outlined" color="primary"
                                  onClick={() => this.acceptRejectConsultDialog(true, props, true)}>
                            <SvgIcon className="marginRight5">
                              <RejectIcon color="primary"/>
                            </SvgIcon>
                            Reject
                          </Button>
                        </>
                      )
                      : <></>
                  }
                </div>
              )
            }
          }
        }
      }];

    return (
      <>
        <Grid item xs={12} className="custom-table-container">
          <MaterialTable
            title={
              <Grid item xs={12} className="table-header-title-container">
                <span className="table-header-title">Appointment Requests</span>
                {
                  loggedInUserPractice.Practice.allowPayment
                  ?(
                    loggedInUserPractice.Practice.stripeConnectAccountStatus
                    ?(
                      <Button
                        className="invite-patient-button"
                        onClick={() => this.addNewConsultRequestDialog(true)}>
                        <SvgIcon className="marginRight5">
                          <InviteUserIcon color="default"/>
                        </SvgIcon>
                        Invite Patient
                      </Button>
                    )
                    :<></>
                  )
                  :(
                    <Button
                      className="invite-patient-button"
                      onClick={() => this.addNewConsultRequestDialog(true)}>
                      <SvgIcon className="marginRight5">
                        <InviteUserIcon color="default"/>
                      </SvgIcon>
                      Invite Patient
                    </Button>
                  )
                }
              </Grid>
            }
            scrollable
            columns={columns}
            data={consults.rows}
            options={{
              search: false,
              pageSizeOptions: [10, 20, 30, 40, 50],
              pageSize: paginationParams.rowsPerPage,
              emptyRowsWhenPaging: false,
              filtering: false,
              draggable: false,
              headerStyle: {
                padding: '2px 40px 2px 16px',
                background: '#F0F0F0A7 0% 0% no-repeat padding-box',
                color: '#848383',
                fontSize: '18px',
                letterSpacing: '0',
              },
              searchFieldStyle: {
                padding: '2px 40px 2px 16px'
              }
            }}
            pages={Math.ceil(consults.count / paginationParams.rowsPerPage)}
            totalCount={consults.count}
            page={paginationParams.page}
            onChangeRowsPerPage={(pageSize) => {
              let data = paginationParams;
              data['rowsPerPage'] = pageSize;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllConsults(data);
              })
            }}
            onChangePage={(page) => {
              let data = paginationParams;
              data['page'] = page;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllConsults(paginationParams);
              })
            }}
          />
        </Grid>

        {
          loggedInUser.isVeterinary
          ?(
            <InviteVeterinaryPatientForm
              visible={this.state.addNewConsultRequest}
              currentRole={this.props.currentRole}
              doctorUsers={this.props.doctorUsers}
              species={this.props.species}
              onCreate={(formData) => this.handleInvitePatient(formData)}
              showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
              onCancel={() => this.addNewConsultRequestDialog(false)}
            />
          )
          :(
            <InvitePatientForm
              visible={this.state.addNewConsultRequest}
              currentRole={this.props.currentRole}
              doctorUsers={this.props.doctorUsers}
              onCreate={(formData) => this.handleInvitePatient(formData)}
              showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
              onCancel={() => this.addNewConsultRequestDialog(false)}
            />
          )
        }

        <AcceptRejectConsultForm
          loggedInUserPractice={this.props.loggedInUserPractice}
          visible={this.state.acceptRejectConsults}
          isReject={this.state.isReject}
          handleAcceptConsult={(formData) => this.handleAcceptConsult(formData)}
          handleRejectConsult={(formData) => this.handleRejectConsult(formData)}
          showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
          consultData={this.state.consultData}
          onCancel={() => this.acceptRejectConsultDialog(false, {})}
        />

        <StartRejectConsultForm
          visible={this.state.startRejectConsults}
          isReject={this.state.isReject}
          handleAcceptConsult={(formData) => this.handleStartConsult(formData)}
          handleRejectConsult={(formData) => this.handleRejectConsult(formData)}
          showSnackbar={(message, variant) => this.props.showSnackbar(message, variant)}
          consultData={this.state.consultData}
          onCancel={() => this.startRejectConsultDialog(false, {})}
        />
      </>
    )
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    consults: state.consults.consults,
    loggedInUserPractice: state.auth.loggedInUserPractice,
    doctorUsers: state.user.doctorUsers,
    species: state.auth.species,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllConsults: (data) => {
      return dispatch(getAllConsultsRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    getAllDoctorUsers: (practiceId) => {
      return dispatch(getAllDoctorUsersRequest(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    getAllSpecies: () => {
      return dispatch(getAllSpeciesRequest()).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    addConsult: (data) => {
      return dispatch(addConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    confirmConsult: (data) => {
      return dispatch(confirmConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    rejectConsult: (data) => {
      return dispatch(rejectConsultRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    startConsult: consultId => {
      return dispatch(startConsult(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },


    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

RequestQueueConsults = connect(
  mapStateToProps,
  mapDispatchToProps
)(RequestQueueConsults);

export default RequestQueueConsults;
