import React, { Component } from 'react';
import { connect } from "react-redux";

import { OTPublisher } from 'opentok-react';
import { Snackbar } from "@material-ui/core";
import { Alert } from "@material-ui/lab";

import './Video.css';
import Microphone from '../../../../assets/microphone_icon.svg';
import MicrophoneSlash from '../../../../assets/microphone_slash_icon.svg';
import Video from '../../../../assets/video_icon.svg';
import VideoSlash from '../../../../assets/video_slash_icon.svg';

import { patientNotAvailableRequest } from '../../ConsultApiActions';

class Publisher extends Component {
  constructor(props) {
    super(props);

    this.state = {
      audio: true,
      video: true
    };
  }

  toggleAudio = () => {
    const audio = !this.state.audio;
    this.setState({ audio });
  };

  toggleVideo = () => {
    const video = !this.state.video;
    this.setState({ video });
  };

  onError = (err) => {
  };

  componentDidMount = () => {
    setTimeout(() => { 
      const consultId = sessionStorage.getItem('consult');
      if(!this.props.subscriberConnected){
        let data={};
        data.consultId = consultId;
        this.props.patientNotAvailable(data);
      }
     }, 10000);
  }

  componentWillReceiveProps = (nextProps) => {
    const consultId = sessionStorage.getItem('consult');
    if(!nextProps.subscriberConnected){
      let data={};
      data.consultId = consultId;
      this.props.patientNotAvailable(data);
    }
  }

  render() {
    const { audio, video } = this.state;
    const {subscriberConnected} = this.props;

    const className = subscriberConnected ? 'video-container' : 'subscriber-not-connected';

    return (
      <div>
        {
          !subscriberConnected
          ?(
            <Snackbar className={'waiting-message-container'} open={true} anchorOrigin={{ vertical: 'top', horizontal: 'middle' }}>
              <Alert severity="info">
                Waiting for the patient to join the consult
              </Alert>
            </Snackbar>
          )
          :<></>
        }
          
        <OTPublisher
          className={className}
          properties={{
            publishAudio: audio,
            publishVideo: video,
            videoSource: undefined
          }}
          onError={this.onError}
        />
        <div className={'video-button-container'}>
          <img src={audio ? Microphone : MicrophoneSlash} alt="Microphone" onClick={this.toggleAudio} className={'margin-horizontal-10'} />
          <img src={video ? Video : VideoSlash} alt="Video" onClick={this.toggleVideo} className={'margin-horizontal-10'} />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {}
};

const mapDispatchToProps = (dispatch) => {
  return {
    patientNotAvailable: data => {
      return dispatch(patientNotAvailableRequest(data)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
  }
}

Publisher = connect(
  mapStateToProps,
  mapDispatchToProps
)(Publisher);

export default Publisher;