import HttpRequest from '../../utils/HttpRequest';
import PracticeHttpRequest from '../../utils/PracticeHttpRequest';
import Auth from '../../utils/Auth';

import { hideLoader, showLoader } from '../../utils/components/AppLoader/AppLoaderActions'

import Constants from '../../utils/Constants';
import { getUserPracticeRequest } from '../User/UserApiActions'
import { signIn, getAllSpecalities, getAllSpecies } from "./AuthenticationActions";


/**
 * Request the server to validate user credentials and get the access token
 * @param credentials
 * @returns {function(*)}
 */
export function signInFacebookRequest(credentials) {
  credentials.role = Constants.SYSTEM_ROLES.ROLE_PRACTICE_ADMIN;
  credentials.access_token = credentials.accessToken;

  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, Constants.ACTIONS.LOGIN_WITH_FACEBOOK, 'post', credentials)
      .send()
      .then(res => {
        let { user, access_token, subscriptionFlag } = res.data.data;
        user['isSubscription'] = subscriptionFlag;
        user['isVeterinary'] = process.env.REACT_APP_IS_VETERINARY==="true"?true:false;
        // Store access token(JWT) and user details inside cookies
        Auth.setToken(access_token);
        Auth.login(user);

        // getUserPracticeRequest method used to get logged in user practice
        dispatch(getUserPracticeRequest(user.id)).then((practiceRes) => {
          dispatch(signIn(res.data.data));
        });

        dispatch(hideLoader());
        return Promise.resolve();
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}

