/**
 * Request the server to validate user credentials and get the access token
 * @param credentials
 * @returns {function(*)}
 */
export function validateUserRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new HttpRequest(dispatch, Constants.ACTIONS.VALIDATE_USER, 'post', data)
      .send()
      .then(res => {
        let { user, access_token } = res.data.data;
        Auth.setToken(access_token);
        Auth.setValidatePatient(user);

        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }, error => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}