import React from 'react';
import './InformationForm.css';
import { connect } from "react-redux";
import { withStyles } from '@material-ui/core/styles';

import { AppBar, Toolbar, Button, Card, CardHeader, CardContent, IconButton, Snackbar,
        CardActions, MenuItem, Grid, Dialog, DialogContent, DialogTitle, Typography } from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import { TextValidator, ValidatorForm } from 'react-material-ui-form-validator';
import {DatePicker, MuiPickersUtilsProvider} from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment-timezone';
import { StripeProvider, Elements } from 'react-stripe-elements';
import CloseIcon from '@material-ui/icons/Close';

import logo from '../../../../assets/svg/header_logo.png';
import UtilHelper from '../../../../utils/UtilHelper';
import Constants, {ONLY_BLANK_SPACES, DECIMAL_INPUT} from '../../../../utils/Constants';
import ConstantMessages from '../../../../utils/Strings';

import { validateUserRequest } from '../../../Authentication/AuthenticationApiActions'
import { getConsultInfo, startConsult } from "../../../Consult/ConsultApiActions";
import { showSnackbar, clearSnackbar } from "../../../../utils/components/AppLoader/AppLoaderActions";
import { makePaymentForConsultRequest } from '../../PatientInformationApiActions';

import BillingForm from '../../../Practice/components/BillingForm/BillingForm';
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import AppLoader from '../../../../utils/components/AppLoader/AppLoader';

import Auth from '../../../../utils/Auth';

const dialogTitleStyles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});
const CustomeDialogTitle = withStyles(dialogTitleStyles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <DialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
});

const styles = theme => ({
  imageLogo: {
    width: '100px',
    height: '45px'
  },
  BillingFormDialogContent:{
      padding: theme.spacing(3),
  }
});

class InformationForm extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            isVeterinary: process.env.REACT_APP_IS_VETERINARY==="true"?true:false,
            patient: {
                email: '',
                dob: '',
                gender: ''
            },
            petPatient: {
                email: '',
                petAge: '',
                petGender: ''
            },
            consult: {},
            consultId: '',
            isPayment: false,
            validDOB: false,
            formSubmit: false,
            showLoader: false
        }
    }

    /**
     * Get consultId from url params
     */
    componentDidMount = () => {
        const token = Auth.getToken();
        const consultId = this.props.match.params.consultId;
        this.setState({ 
            consultId 
        }, () => {
            if(token && Auth.isLoggedIn()){
                this.logoutUser();
            }else{
                if(token){
                    this.props.getConsultInfo(this.state.consultId).then(res => {
                        this.setState({ consult: res.data[0] }, () => {
                            if(this.state.consult.status === 'ENDED'){
                                Auth.logoutValidatePatient();
                            }
                        });
                    });
                }else{}   
            }
        
        });

        ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
        ValidatorForm.addValidationRule(DECIMAL_INPUT, UtilHelper.validAgeInput);
    }

    /**
     * Remove custom validation in ValidatorForm rule when component is unmount
     * AttachComponent method call when component is render
     */
    componentWillUnmount() {
        ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
        ValidatorForm.removeValidationRule(DECIMAL_INPUT);
    }

    /*
    * Method executes when request to logout
    */
    logoutUser = () => {
        this.refs.messageModal.setState({
        showModal: true, message: 'Looks like you are already logged in other tab, so please logout and continue.', title: 'Already Login',
        showOkButton: true,
        showCancelButton: false,
        handleOk: () => {
            Auth.logout();
        },
        handleCancel: () => {
        },
        okText: 'Logout',
        });
    };

    /**
     * Method is used for hide loader
     */
    hideLoader = () => {
        this.setState({ showLoader: false });
    };

    /**
     * Method is used for show loader
     */
    showLoader = () => {
        this.setState({ showLoader: true });
    };

    /**
     * Method used for start the consult
     */
    startConsult = (consultId, stripePaymentIntentId) => {
        let data = {
            consultId: consultId
        }
        if(stripePaymentIntentId){
            data.stripePaymentIntentId= stripePaymentIntentId
        }else{}
        this.props.startConsult(consultId).then(res => {
            sessionStorage.setItem('patientConsult', consultId);
            window.location = '/appointment/consult';
        }, error => {
          this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
        });
    }

    checkForPayment = () => {
        this.props.getConsultInfo(this.state.consultId).then(res => {
            this.setState({ 
                consult: res.data[0] 
            }, () => {
                if(this.state.consult.doctorId && this.state.consult.status !== 'ENDED'){
                    if(this.state.consult.allowPayment && this.state.consult.stripeConnectAccountStatus){
                        if(!this.state.consult.stripePaymentIntentId && parseInt(this.state.consult.consultationFee) > 0){
                            this.setState({ isPayment: true });
                        }else{
                            this.startConsult(this.state.consultId, undefined);
                        }
                    }else{
                        this.startConsult(this.state.consultId, undefined);         
                    }
                }else if(this.state.consult.status === 'ENDED'){
                    Auth.logoutValidatePatient();
                }
                // this.startConsult(this.state.consultId)
            });
        }, error => {
            this.props.showSnackbar(ConstantMessages.somethingWentWrong, Constants.SNACKBAR_VARIAINT.ERROR);
        });
    }

    /**
     * Method executes on onChange action of form element
     */
    handleChange = (event) => {
        if(this.state.isVeterinary){
            const petPatient = Object.assign({}, this.state.petPatient);
            petPatient[event.target.name] = event.target.value;
            this.setState({ petPatient });
        }else{
            const patient = Object.assign({}, this.state.patient);
            patient[event.target.name] = event.target.value;
            this.setState({ patient });
        }
    };

    /**
   * Method executes on onChange action of date picker form element
   */
    handleDateChange = (date) => {
        const patient = Object.assign({}, this.state.patient);
        patient['dob'] = moment(date).format('DD/MM/YYYY');
        this.setState({patient, validDOB: true});
    };

    /**
     * Method executes on submit form action
     */
    handleSubmit = () => {
        this.setState({ formSubmit: true }, () => {
            let data;
            if(this.state.isVeterinary){
                data = Object.assign({}, this.state.petPatient);
                data.consultId = this.state.consultId;
                this.props.validateUser(data).then((res) => {
                    this.checkForPayment();
                });
            }else{
                data = Object.assign({}, this.state.patient);
                data.consultId = this.state.consultId;
                if(this.state.validDOB){
                    this.props.validateUser(data).then((res) => {
                        this.checkForPayment();
                    });
                }
            }
        })
    };

    /**
     * Method is used for process the payment request on stripe
     */
    makePaymentForConsult = (token) => {
        let data = {};
        data.patientId = this.state.consult.bookingPatientId;
        data.practiceId = this.state.consult.practiceId;
        data.doctorId = this.state.consult.doctorId;
        data.consultId  = this.state.consultId;
        data.amount = this.state.consult.consultationFee;
        data.currency = this.state.consult.currency;
        data.token = token;
        this.props.makePaymentForConsult(data).then((res) => {
            if(res){
                this.setState({
                    isPayment: false
                }, () => {
                    if(res.status === "succeeded"){
                        this.startConsult(this.state.consultId, res.stripePaymentIntentId);
                    }else{
                        this.props.showSnackbar(ConstantMessages.consultPaymentNotSuccessMessage, Constants.SNACKBAR_VARIAINT.WARNING);
                    }    
                })
            }
        })
    }
    
    /**
     * Method is used to get the token from the stripe for the transaction
     */
    handleTokenCreation = (token) => {
        if (token.hasOwnProperty('token') && token.token.hasOwnProperty('id')) {
            this.setState({ showLoader: true }, () => {
                this.makePaymentForConsult(token.token.id);
            });
        } else {
            this.hideLoader();
        }
    };
    /**
     * Method used to close the snackbar
     */
    handleCloseSnackbar = () => {
        this.props.clearSnackbar();
    };

    render(){
        const { classes } = this.props;
        const { consult, patient, petPatient } = this.state;
        return(
            <>
                <AppBar
                color="inherit"
                position="fixed">
                    <Toolbar>
                        <img alt="Logo" src={logo} className={classes.imageLogo} />
                    </Toolbar>
                </AppBar>
                <main className={'information-form-content'}>
                    <Grid container spacing={3} className="verticalAligMiddle"
                    justify="center"
                    alignItems="center"
                    direction="row"
                    style={{ minHeight: '100%' }}>
                        <Grid item xl={4} lg={4} md={12} sm={12} xs={12} ></Grid>
                        <Grid item xl={4} lg={4} md={12} sm={12} xs={12} >
                            {
                                this.state.consult.status === 'ENDED'
                                ?(
                                    <Card className="textAlignCenter padding20">
                                        <CardHeader className="cardInfoHeader" title={<Typography variant="h1">
                                                Thank You!
                                            </Typography>}/>
                                        <CardContent className="cardContent">
                                            <br />
                                            <Typography variant="subtitle1">
                                                Your appointment {consult.doctorName?'with Dr. '+consult.doctorName:''} is ended.
                                            </Typography>
                                            <br />
                                            <Typography variant="subtitle1">
                                                { process.env.REACT_APP_THANK_YOU_MESSAGE_APPLESS_CONSULT }
                                            </Typography>
                                        </CardContent>
                                    </Card>
                                )
                                :(
                                    this.state.isVeterinary
                                    ?(
                                        <ValidatorForm
                                            ref="form"
                                            onSubmit={this.handleSubmit}
                                            noValidate
                                            onError={errors => console.log(errors)}
                                        >
                                            <Card className="padding20">
                                                <CardHeader className="cardInfoHeader" title=" Verify your consult details"
                                                    subheader="Please enter your consult information"/>
                                                <CardContent className="cardContent">
                                                    <TextValidator
                                                        fullWidth
                                                        required
                                                        label="Email Address" onChange={this.handleChange}
                                                        name="email" value={petPatient.email || ''} validators={['required', 'isEmail']}
                                                        errorMessages={['this field is required', 'email is not valid']}
                                                    />
                                                    
                                                    <TextValidator
                                                        required
                                                        fullWidth
                                                        className="marginTop10"
                                                        label="Pet Age" onChange={this.handleChange}
                                                        name="petAge" value={petPatient.petAge || ''}
                                                        validators={['required', ONLY_BLANK_SPACES, DECIMAL_INPUT, 'minFloat:' + Constants.VALIDATIONS.MIN_PET_AGE, 'maxNumber:' + Constants.VALIDATIONS.MAX_PET_AGE]}
                                                        errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, Constants.ERROR_MESSAGES.valid_age_input, ConstantMessages.minimum_allowed_number_pet_age_message, ConstantMessages.maximum_allowed_number_pet_age_message]}
                                                    />
                                                    
                                                    <TextValidator
                                                        fullWidth
                                                        required
                                                        select
                                                        className="marginNormal"
                                                        label="Pet Gender" onChange={this.handleChange}
                                                        name="petGender" value={petPatient.petGender || ''} validators={['required']}
                                                        errorMessages={['this field is required']}
                                                    >
                                                        <MenuItem value="Male">Male</MenuItem>
                                                        <MenuItem value="Female">Female</MenuItem>
                                                    </TextValidator>
                                                </CardContent>
                                                <CardActions className="marginNormal">
                                                    <Button fullWidth type="submit" variant="contained" color="primary">
                                                        Verify and start consult
                                                    </Button>
                                                </CardActions>
                                            </Card>
                                        </ValidatorForm>
                                    )
                                    :(
                                        <ValidatorForm
                                            ref="form"
                                            onSubmit={this.handleSubmit}
                                            noValidate
                                            onError={errors => console.log(errors)}
                                        >
                                            <Card className="padding20">
                                                <CardHeader title=" Verify your consult details"
                                                    subheader="Please enter your consult information"/>
                                                <CardContent>
                                                    <TextValidator
                                                        fullWidth
                                                        required
                                                        label="Email Address" onChange={this.handleChange}
                                                        name="email" value={patient.email || ''} validators={['required', 'isEmail']}
                                                        errorMessages={['this field is required', 'email is not valid']}
                                                    />
                                                    
                                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                        <DatePicker
                                                            required
                                                            fullWidth
                                                            className="dob"
                                                            margin="normal"
                                                            id="date-picker-dialog"
                                                            label="Date Of Birth"
                                                            format="MM-dd-yyyy"
                                                            defaultChecked={"01-01-2000"}
                                                            initialFocusedDate={moment("01-01-2000")}
                                                            openTo="year"
                                                            views={["year", "month", "date"]}
                                                            maxDate={moment(new Date())}
                                                            value={patient.dob ? moment(patient.dob, 'DD-MM-YYYY') : null}
                                                            onChange={this.handleDateChange}
                                                        />
                                                        {
                                                            this.state.formSubmit && !this.state.validDOB ?
                                                            <div className="errorMessageForPatient">Please select date of birth</div> : ''
                                                        }
                                                    </MuiPickersUtilsProvider>

                                                    <TextValidator
                                                        fullWidth
                                                        required
                                                        select
                                                        className="marginNormal"
                                                        label="Gender" onChange={this.handleChange}
                                                        name="gender" value={patient.gender || ''} validators={['required']}
                                                        errorMessages={['this field is required']}
                                                    >
                                                        <MenuItem value="Male">Male</MenuItem>
                                                        <MenuItem value="Female">Female</MenuItem>
                                                    </TextValidator>
                                                </CardContent>
                                                <CardActions className="marginNormal">
                                                    <Button fullWidth type="submit" variant="contained" color="primary">
                                                        Verify and start consult
                                                    </Button>
                                                </CardActions>
                                            </Card>
                                        </ValidatorForm>
                                    )
                                )
                            }
                        </Grid>
                        <Grid item xl={4} lg={4} md={12} sm={12} xs={12} ></Grid>
                    </Grid>

                    <Dialog
                        maxWidth="xs"
                        fullWidth={true}
                        open={this.state.isPayment}
                        aria-labelledby="responsive-dialog-title"
                        className={classes.BillingFormDialogContent}
                    >
                        <CustomeDialogTitle id="responsive-dialog-title" onClose={this.handleClose}>
                            <Typography className="cardDetaiulsMainHeader" color="primary">
                                Card Details
                            </Typography>
                            <Typography className="cardDetaiulsSecondHeader" color="primary">
                                Enter card details below to proceed with payment for consult
                            </Typography>
                        </CustomeDialogTitle>
                        <DialogContent>
                            <StripeProvider apiKey={process.env.REACT_APP_STRIPE_API_KEY}>
                                <Elements>
                                    <BillingForm consultationFee={this.state.consult.consultationFee} consult={this.state.consult} handleTokenCreation={(data) => this.handleTokenCreation(data)} showLoader={this.showLoader} hideLoader={this.hideLoader} />
                                </Elements>
                            </StripeProvider>
                        </DialogContent>
                    </Dialog>

                    <MessageModal
                        ref="messageModal" />
                    <AppLoader showLoader={this.state.showLoader} />
                    <Snackbar open={this.props.showSnackbar} autoHideDuration={3000}
                        onClose={() => this.handleCloseSnackbar()}
                        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
                        <Alert severity={this.props.snackbarVariant}>
                        {this.props.snackbarMessage}
                        </Alert>
                    </Snackbar>
                
                </main>
            </>
        )
    }

}

const mapStateToProps = (state) => {
    return {
      showSnackbar: state.showLoader.showSnackbar,
      snackbarVariant: state.showLoader.snackbarVariant,
      snackbarMessage: state.showLoader.snackbarMessage,
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        validateUser: (data) => {
            return dispatch(validateUserRequest(data)).then((res) => {
                return Promise.resolve(res);
            }, (error) => {
              return Promise.reject(error);
            });
        },
        getConsultInfo: consultId => {
          return dispatch(getConsultInfo(consultId)).then(res => {
            return Promise.resolve(res);
          }, error => {
            return Promise.reject(error);
          });
        },

        showSnackbar: (message, variant) => {
          dispatch(showSnackbar(message, variant));
        },

        makePaymentForConsult: (data) => {
            return dispatch(makePaymentForConsultRequest(data)).then(res => {
                return Promise.resolve(res);
            }, error => {
                return Promise.reject(error);
            });
        },

        startConsult: consultId => {
            return dispatch(startConsult(consultId)).then(res => {
              return Promise.resolve(res);
            }, error => {
              return Promise.reject(error);
            });
        },

        clearSnackbar: () => {
          dispatch(clearSnackbar())
        }
    }
}

InformationForm = connect(
    mapStateToProps,
    mapDispatchToProps
)(InformationForm);

export default withStyles(styles)(InformationForm);