import React from 'react';
import './AvatarCell.css';
import {Avatar} from "@material-ui/core";

const AvatarCell = props => {
  const avatar = props.heading.split(' ').map((item, index) => {
    if (index < 2)
      return item.substr(0, 1);
    return '';
  });
  const avatarDetailsClass = props.subHeading ? "avatar-details-top" : "avatar-details-middle";

  return (
    <div className={"avatar-cell-container"}>
      <div className={"avatar-container"}>
        <Avatar>{avatar.join('')}</Avatar>
      </div>
      <div className={"avatar-container avatar-details " + avatarDetailsClass}>
        <div className="custom-user-display-name">{props.heading}</div>
        {props.subHeading && (
          <div className="custom-user-name-sub-heading">{props.subHeading}</div>
        )}
      </div>
    </div>
  );
};

export default AvatarCell;
