import React from 'react';
import { connect } from 'react-redux';
import { Button, Grid, Chip, Avatar } from '@material-ui/core';

import MaterialTable from 'material-table';
import { setPageTitle } from "../../../DashboardActions";
import Constants from '../../../../../utils/Constants';
import MessageModal from "../../../../../utils/components/MessageModal/MessageModal";
import {
  getAllHistoricalConsultsRequest
} from '../../../../Consult/ConsultApiActions';

import SvgIcon from '@material-ui/core/SvgIcon';
import {ReactComponent as TimeIcon} from '../../../../../assets/svg/timeIcon.svg';
import AvatarCell from "../../../../../utils/components/AvatarCell/AvatarCell";

class HistoricalConsults extends React.Component {

  constructor(props) {
    super(props);
    this.paginationParams = {
      page: 0,
      rowsPerPage: Constants.ROW_PER_PAGE_5,
    };
    this.state = {
      paginationParams: Object.assign({}, this.paginationParams)
    }
  };

  /**
   * Set page title action call
   * Get all Appointments History method call when component is render
   * AttachComponent method call when component is render
   */
  componentDidMount = () => {
    this.props.updatePageTitle("Appointments History", false);
    let data = this.state.paginationParams;
    this.props.getAllHistoricalConsults(data)
  };

  /**
   * Method is used show the consult history
   */
  viewConsult = (consultId) => {
    sessionStorage.setItem('consultHistory', consultId);
    window.location = '/consultHistory';
  };

  /**
   * Method is used show the reason of reject consult
   */
  viewReason = (consultData) => {
    this.refs.messageModal.setState({
      showModal: true,
      message: consultData.rejectReason,
      title: 'Rejection Reason',
      showOkButton: true,
      handleOk: () => {
      },
      okText: 'Ok'
    });
  };

  render() {
    const { historicalConsults } = this.props;
    const { paginationParams } = this.state;

    const columns = [{
      title: this.props.loggedInUser.isVeterinary?'Owner Name':'Patient Name',
      fiels: 'name',
      filterable: false,
      draggable: false,
      sorting: false,
      render: props => {
        if(props.patientDetails && props.patientDetails.name){
          return (<AvatarCell heading={props.patientDetails.name} />);
        }else{
          return "-";
        }
      }
    },
    {
      title: 'Pet Name',
      fiels: 'petName',
      filterable: false,
      draggable: false,
      sorting: false,
      hidden: !this.props.loggedInUser.isVeterinary,
      render: props => {
        if(props.patientDetails && props.patientDetails.petName) {
          return (<span className="custom-column-heading">{ props.patientDetails.petName }</span>);
        }else{
          return "-";
        }
      }
    },
    {
      title: 'Appointment Details',
      field: 'appointmentTime',
      filterable: false,
      draggable: false,
      sorting: false,
      render: props => {
        return (
          <span className="custom-column-heading">
            {
              props.status !== "REJECTED"
                ? props.appointmentTime
                ? (
                  <span className="isTime">
                    <SvgIcon className="isTime isTimeIcon">
                      <TimeIcon className="isTime"/>
                    </SvgIcon>
                    <span className="custom-column-heading">{props.appointmentTime ? props.appointmentTime : "Not Available"}</span>
                  </span>
                )
                :"-"
                : "-"
            }
          </span>
        )
      }
    },
    {
      title: 'Doctor Name',
      field: 'doctorName',
      filterable: false,
      draggable: false,
      sorting: false,
      hidden: !this.props.loggedInUser.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_COORDINATOR),
      render: props => {
        return (
          <span className="custom-column-heading">
            {props.doctorName ? props.doctorName : '-'}
          </span>
        )
      }
    },
    {
      title: 'Status',
      field: 'status',
      filterable: false,
      draggable: false,
      sorting: false,
      render: props => {
        return (
          <Chip variant="outlined" color="primary" size="small" label={props.status} />
        )
      }
    },
    {
      title: 'Actions',
      maxWidth: 150,
      filterable: false,
      draggable: false,
      sorting: false,
      field: 'id',
      sortable: false,
      hidden: !this.props.loggedInUser.Roles.find((role) => role.name === Constants.SYSTEM_ROLES.ROLE_DOCTOR),
      render: props => {
        if (props.status === "REJECTED") {
          return (
            <div>
              <Button className="view-consult-button" size="small" variant="outlined" color="primary" onClick={() => this.viewReason(props)}>Reason</Button>
            </div>
          )
        } else {
          return (
            <div>
              <Button size="small" className="view-consult-button" variant="outlined" color="primary" onClick={() => this.viewConsult(props.id)}>View</Button>
            </div>
          )
        }
      }
    }
    ];

    return (
      <>
        <Grid item xs={12} className="custom-table-container">
          <MaterialTable
            title={
              <Grid item xs={12} className="table-header-title-container">
                <span className="table-header-title">Appointments History</span>
              </Grid>
            }
            scrollable
            columns={columns}
            data={historicalConsults.rows}
            options={{
              search: false,
              pageSizeOptions: [10, 20, 30, 40, 50],
              pageSize: paginationParams.rowsPerPage,
              emptyRowsWhenPaging: false,
              filtering: false,
              draggable: false,
              headerStyle: {
                padding: '2px 40px 2px 16px',
                background: '#F0F0F0A7 0% 0% no-repeat padding-box',
                color: '#848383',
                fontSize: '18px',
                letterSpacing: '0',
              },
              searchFieldStyle: {
                padding: '2px 40px 2px 16px'
              }
            }}
            pages={Math.ceil(historicalConsults.count / paginationParams.rowsPerPage)}
            totalCount={historicalConsults.count}
            page={paginationParams.page}
            onChangeRowsPerPage={(pageSize) => {
              let data = this.state.paginationParams;
              data['rowsPerPage'] = pageSize;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllHistoricalConsults(data);
              })
            }}
            onChangePage={(page) => {
              let data = this.state.paginationParams;
              data['page'] = page;
              this.setState({
                paginationParams: data
              }, () => {
                this.props.getAllHistoricalConsults(this.state.paginationParams);
              })

            }}
          />
        </Grid>
        <MessageModal
          ref="messageModal" />
      </>
    )
  }

}

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    historicalConsults: state.consults.historicalConsults,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getAllHistoricalConsults: (data) => {
      return dispatch(getAllHistoricalConsultsRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    updatePageTitle: (pageTitle, enablePageTitle) => {
      dispatch(setPageTitle(pageTitle, enablePageTitle));
    }
  }
};

HistoricalConsults = connect(
  mapStateToProps,
  mapDispatchToProps
)(HistoricalConsults);

export default HistoricalConsults;
