import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SoapForm from "../SoapForm/SoapForm";
import Document from "../Document/Document";
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import './ConsultForm.css';
import ConstantMessages from "../../../../utils/Strings";
import Constants from "../../../../utils/Constants";
import {connect} from "react-redux";
import {deleteConsultFile, downloadConsultFile, getConsultFiles, uploadConsultFile} from "../../ConsultApiActions";
import {showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";

const styles = theme => ({
  marginTopAddRemove: {
    [theme.breakpoints.down('xs')]: {
      marginTop: '0px'
    },
    [theme.breakpoints.up('sm')]: {
      marginTop: '0px'
    },
    [theme.breakpoints.up('md')]: {
      marginTop: '64px'
    },
    [theme.breakpoints.up('lg')]: {
      marginTop: '64px'
    },
    [theme.breakpoints.up('xl')]: {
      marginTop: '64px'
    },
  }
});

const TabPanel = (props) => {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`consult-tabpanel-${index}`}
      aria-labelledby={`consult-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
};

class ConsultForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: 0,
      patientFiles: [],
      doctorFiles: [],
    };
  }

  handleChange = (event, newValue) => {
    this.setState({ value: newValue });
  };

  sortFiles = files => {
    const patientFiles = [];
    const doctorFiles = [];
    files.forEach(file => {
      if (file.createdBy === this.props.loggedInUser.id) {
        doctorFiles.push(file);
      } else {
        patientFiles.push(file);
      }
    });

    this.setState({ patientFiles, doctorFiles });
  };

  getConsultFiles = () => {
    this.props.getConsultFiles(this.props.consultId)
      .then(res => {
        this.sortFiles(res.data);
      }, error => {
        this.props.showSnackbar(ConstantMessages.unableToGetConsultDocuments, Constants.SNACKBAR_VARIAINT.ERROR);
      });
  };

  addFile = file => {
    file.createdBy = this.props.loggedInUser.id;
    this.setState({doctorFiles: this.state.doctorFiles.concat(file)});
  };

  removeFile = fileId => {
    const doctorFiles = this.state.doctorFiles.filter(file => file.fileId !== fileId);
    this.setState({ doctorFiles });
  };

  componentDidMount() {
    this.getConsultFiles();
  }

  render() {
    const {value, doctorFiles, patientFiles} = this.state;
    const { classes } = this.props;

    return (
      <>
        <AppBar position="static" className={`${classes.marginTopAddRemove} consult-tabs`}>
          <Tabs
            value={value}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered>
            <Tab label="Notes" />
            <Tab label={"Documents (" + (doctorFiles.length + patientFiles.length) + ")"} />
          </Tabs>
        </AppBar>

        <TabPanel value={value} index={0}>
          <SoapForm consultId={this.props.consultId} />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Document
            consultId={this.props.consultId}
            addFile={this.addFile}
            removeFile={this.removeFile}
            doctorFiles={doctorFiles}
            patientFiles={patientFiles}
          />
        </TabPanel>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getConsultFiles: consultId => {
      return dispatch(getConsultFiles(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};


ConsultForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConsultForm);

export default withStyles(styles)(ConsultForm);
