import PaymentHttpRequest from '../../utils/PaymentHttpRequest';
import ConsultHttpRequest from '../../utils/ConsultHttpRequest';

import { SNACKBAR_VARIAINT } from '../../utils/Constants';
import { hideLoader, showLoader, showSnackbar } from '../../utils/components/AppLoader/AppLoaderActions';
import ConstantMessages from '../../utils/Strings';

/**
 * Server API call to pay amount for consults
 * @returns {function(*)}
 */
export function makePaymentForConsultRequest(data) {
    return (dispatch) => {
      dispatch(showLoader());
      return new PaymentHttpRequest(dispatch, `/payment/makeConsultPayment`, 'post', data)
        .send()
        .then(res => {
          dispatch(showSnackbar(ConstantMessages.makePaymentForConsultSuccessMessage, SNACKBAR_VARIAINT.SUCCESS));
          dispatch(hideLoader());
          return Promise.resolve(res.data.data);
        }).catch((error) => {
          dispatch(hideLoader());
          return Promise.reject(error);
        });
    };
}

/**
 * Server API call to get status of consult payment
 * @returns {function(*)}
 */
export function getPaymentStatusForConsultRequest(data) {
  return (dispatch) => {
    dispatch(showLoader());
    return new ConsultHttpRequest(dispatch, `/consult/payment/status`, 'put', data)
      .send()
      .then(res => {
        dispatch(hideLoader());
        return Promise.resolve(res.data.data);
      }).catch((error) => {
        dispatch(hideLoader());
        return Promise.reject(error);
      });
  };
}