import React from 'react';
import ConstantMessages from "../../../../utils/Strings";
import { getConsultFiles, deleteConsultFile, downloadConsultFile } from "../../ConsultApiActions";
import { connect } from "react-redux";
import './Document.css';

import Constants from '../../../../utils/Constants';
import { showSnackbar } from "../../../../utils/components/AppLoader/AppLoaderActions";
import SingleFile from "./SingleFile";
import { Dialog, Typography, Grid } from "@material-ui/core";
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import Auth from '../../../../utils/Auth';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(0),
  },
}))(MuiDialogContent);

class Document extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      previewVisible: false,
      previewImage: '',
      loggedInUser: Auth.getValidatePatient()
    };
  }

  handlePreview = async fileId => {
    const previewImage = process.env.REACT_APP_CONSULT_SERVER_API_URL + `/consult/consultFiles/${fileId}`;
    this.setState({ previewVisible: true, previewImage: previewImage });
  };

  handleCancel = () => {
    this.setState({ previewVisible: false, previewImage: '' });
  };

  handleRemove = async fileId => {
    this.props.deleteConsultFile(fileId)
      .then(res => {
        this.props.removeFile(fileId);
      }, error => {
        this.props.showSnackbar(ConstantMessages.unableToDeleteFile, Constants.SNACKBAR_VARIAINT.ERROR);
      });
  };

  render() {
    const {previewVisible, previewImage, loggedInUser} = this.state;
    const {doctorFiles, patientFiles} = this.props;

    return (
      <div className="clearfix">
        <div>
          <Grid item xs={12} sm={12} className="document-title consult-documents">
            Shared by Patient
          </Grid>
          <Grid container spacing={1}>
            {patientFiles && patientFiles.map(file => (
              <Grid key={file.fileId} item xs={4}>
                <SingleFile file={file} handleRemove={this.handleRemove} handlePreview={this.handlePreview} userId={loggedInUser.id} />
              </Grid>
            ))}
          </Grid>
          {
            patientFiles && patientFiles.length === 0 &&
            <Grid className="documents-not-available">Documents not available</Grid>
          }

          <Grid item xs={12} sm={12} className="document-title consult-documents">
            Shared by Clinician
          </Grid>
          <Grid container spacing={1}>
            {doctorFiles && doctorFiles.map(file => (
              <Grid key={file.fileId} item xs={4}>
                <SingleFile file={file} handleRemove={this.handleRemove} handlePreview={this.handlePreview} userId={loggedInUser.id} />
              </Grid>
            ))}
          </Grid>
          {
            doctorFiles && doctorFiles.length === 0 &&
            <Grid className="documents-not-available">Documents not available</Grid>
          }
        </div>

        <Dialog aria-labelledby="customized-dialog-title" open={previewVisible} maxWidth="md" fullWidth={true}>
          <DialogTitle id="customized-dialog-title" onClose={this.handleCancel}>
            Preview
          </DialogTitle>
          <DialogContent dividers>
            <img alt="example" style={{ width: '100%' }} src={previewImage} />
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    deleteConsultFile: consultId => {
      return dispatch(deleteConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    downloadConsultFile: consultId => {
      return dispatch(downloadConsultFile(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    getConsultFiles: consultId => {
      return dispatch(getConsultFiles(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

Document = connect(
  mapStateToProps,
  mapDispatchToProps
)(Document);

export default Document;
