import React, { Component } from 'react';

import { OTSubscriber } from 'opentok-react';

export default class Subscriber extends Component {
  render() {
    return (
      <OTSubscriber
        retry={true}
        maxRetryAttempts={3}
        retryAttemptTimeout={2000}
        onSubscribe={() => {
          this.props.updateSubscriberStatus(true);
        }}
      />
    );
  }
}
